import { Center, ChakraProvider, Grid, GridItem } from "@chakra-ui/react";
import { useState } from "react";

function App() {
  const [ecuation, setEcuation] = useState("")
  const [current, setCurrent] = useState(0)
  const [previous, setPrevious] = useState("")

  const handleClick = (e) =>{
    let targetContent = e.target.textContent
    if ( targetContent === "AC") {
      setEcuation("")
      setCurrent(0)
    }else if(targetContent === "/" || targetContent === "+" || targetContent === "-" || targetContent === "*" ){
      if(ecuation.endsWith("/-") || ecuation.endsWith("+-") || ecuation.endsWith("*-") || ecuation.endsWith("--") ){
        let temporal = ecuation.substring(0,ecuation.length -2)
        setEcuation(temporal + targetContent)
        setCurrent(targetContent)
      }
      else if(previous === "="){
        setEcuation(current + targetContent)
        setCurrent(targetContent)
      }else if(previous === "/" || previous === "+" || previous === "-" || previous === "*" ){
        if(previous !== "-" && targetContent !== "-" ){
          let temporal = ecuation.substring(0,ecuation.length -1)
          setEcuation(temporal + targetContent)
          setCurrent(targetContent)
        }else{
          setEcuation(ecuation + targetContent)
          setCurrent(targetContent)
        }
        
      }
      else{
        setCurrent(targetContent)
        setEcuation(ecuation + targetContent)
      }
      
    }
    else if (current === 0) {
      if(targetContent === "0"){
        setCurrent(0)
      }else{
        setCurrent(targetContent)
        setEcuation(ecuation + targetContent)
      }
      
    }
    else if(targetContent === "="){
      try {
        let calculus = eval(ecuation)
        setEcuation(ecuation + "=" + calculus)
        setCurrent(calculus)
      } catch (error) {
        setEcuation("")
        setCurrent(0)
      }
    }
    else{
      if(!(current.includes(".") && targetContent === ".")){
        setCurrent(current + targetContent)
        setEcuation(ecuation + targetContent)
      }
      
    }
    setPrevious(targetContent)
  }


  return (
    <ChakraProvider>
      <Center bg="teal.400" h="100vh" w="100vw" >
        <Grid gap={0.5} padding={1} templateColumns="1fr 1fr 1fr 1fr" w="300px" templateRows="repeat(6, 60px)" bg="blackAlpha.800" >
          <GridItem display="flex" flexDirection="column-reverse" alignItems="flex-end" colSpan={4} color="white"  ><p id="display" >{current}</p>
          <p>{ecuation}</p></GridItem>
          <GridItem id="clear" cursor="pointer" onClick={(e) => handleClick(e)} display="flex" justifyContent="center" alignItems="center" colSpan={2} color="white" textAlign="center" bg="red.400" >AC</GridItem>
          
          <GridItem id="divide" onClick={(e) => handleClick(e)} cursor="pointer" display="flex" justifyContent="center" alignItems="center" colSpan={1} color="white" textAlign="center" bg="gray.500" >/</GridItem>
          
          <GridItem id="multiply" onClick={(e) => handleClick(e)} cursor="pointer" display="flex" justifyContent="center" alignItems="center" colSpan={1} color="white" textAlign="center" bg="gray.500" >*</GridItem>
          
          <GridItem id="seven" onClick={(e) => handleClick(e)} cursor="pointer" display="flex" justifyContent="center" alignItems="center" colSpan={1} color="white" textAlign="center" bg="gray.700">7</GridItem>
          
          <GridItem id="eight" onClick={(e) => handleClick(e)} cursor="pointer" display="flex" justifyContent="center" alignItems="center" colSpan={1} color="white" textAlign="center" bg="gray.700">8</GridItem>
          
          <GridItem id="nine" onClick={(e) => handleClick(e)} cursor="pointer" display="flex" justifyContent="center" alignItems="center" colSpan={1} color="white" textAlign="center" bg="gray.700">9</GridItem>
          
          <GridItem id="subtract" onClick={(e) => handleClick(e)} cursor="pointer" display="flex" justifyContent="center" alignItems="center" colSpan={1} color="white" textAlign="center" bg="gray.500">-</GridItem>
          
          <GridItem id="four" onClick={(e) => handleClick(e)} cursor="pointer" display="flex" justifyContent="center" alignItems="center" colSpan={1} color="white" textAlign="center" bg="gray.700">4</GridItem>
          
          <GridItem id="five" onClick={(e) => handleClick(e)} cursor="pointer" display="flex" justifyContent="center" alignItems="center" colSpan={1} color="white" textAlign="center" bg="gray.700">5</GridItem>
          
          <GridItem id="six" onClick={(e) => handleClick(e)} cursor="pointer" display="flex" justifyContent="center" alignItems="center" colSpan={1} color="white" textAlign="center" bg="gray.700">6</GridItem>
          
          <GridItem id="add" onClick={(e) => handleClick(e)} cursor="pointer" display="flex" justifyContent="center" alignItems="center" colSpan={1} color="white" textAlign="center" bg="gray.500">+</GridItem>
          
          <GridItem id="one" onClick={(e) => handleClick(e)} cursor="pointer" display="flex" justifyContent="center" alignItems="center" colSpan={1} color="white" textAlign="center" bg="gray.700">1</GridItem>
          
          <GridItem id="two" onClick={(e) => handleClick(e)} cursor="pointer" display="flex" justifyContent="center" alignItems="center" colSpan={1} color="white" textAlign="center" bg="gray.700">2</GridItem>
          
          <GridItem id="three" onClick={(e) => handleClick(e)} cursor="pointer" display="flex" justifyContent="center" alignItems="center" colSpan={1} color="white" textAlign="center" bg="gray.700">3</GridItem>
         
          <GridItem onClick={(e) => handleClick(e)} cursor="pointer" display="flex" justifyContent="center" alignItems="center" rowSpan={2} color="white" textAlign="center" bg="teal.800" id="equals">=</GridItem>
          
          <GridItem onClick={(e) => handleClick(e)} cursor="pointer" display="flex" justifyContent="center" alignItems="center" colSpan={2} color="white" textAlign="center" bg="gray.700" id="zero" >0</GridItem>
          
          <GridItem id="decimal" onClick={(e) => handleClick(e)} cursor="pointer" display="flex" justifyContent="center" alignItems="center" colSpan={1} color="white" textAlign="center" bg="gray.700">.</GridItem>


        </Grid>
      </Center>
    </ChakraProvider>
  );
}

export default App;
